import React, { useEffect, useState } from "react";
import Timestamp from "react-timestamp";
import Comments from "./Comments.js";
import "../styles/tweet.css";

const Tweet = props => {
  let comments = [];
  useEffect(() => {
    let formated = [];
    formated[0] = props.tweet;
    if (formated[0].length === 1) {
      comments = formated[0][0].split("&?");
    } else {
      comments = formated[0].split("&?");
    }
    setComment(comments);
  }, [props.tweet]);
  const [comment, setComment] = useState([]);
  const [commentsOpen, setCommentsOpen] = useState(false);
  const [inputValue, setInputValue] = useState("");

  function handleChange(event) {
    setInputValue(event.target.value);
  }
  return (
    <React.Fragment>
      <li className='list-item'>
        <div className='tweet-picture-container'>
          <img src={props.profilePicture} alt='profile' />
        </div>
        <div className='tweet-content'>
          <span className='tweet-username'>{props.usernameShort}</span>
          <Timestamp
            className='tweet-time'
            time={props.timesOfTweet}
            autoUpdate
          />
          <p>{comment[0]}</p>
        </div>
      </li>
      <div className='comments-input'>
        {commentsOpen ? (
          <input
            type='text'
            placeholder='Comment'
            className='comments-input-open'
            value={inputValue}
            onChange={handleChange}
            onKeyDown={event => props.comment(props, inputValue, event.keyCode)}
          />
        ) : (
          <input placeholder='Comment' className='comments-input-closed' />
        )}
        <button onClick={() => setCommentsOpen(!commentsOpen)}>Comments</button>
      </div>

      <div
        className={
          commentsOpen ? "comments comments-open" : "comments comments-closed"
        }
      >
        {comment.map((comment, index) =>
          index === 0 ? null : (
            <div>
              <Comments comment={comment} />
            </div>
          )
        )}
      </div>
    </React.Fragment>
  );
};

export default Tweet;
